*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.home-section{
    width: 80%;
    height: 45vw;
    display: flex;
    align-items: center;
    margin: auto;
    justify-content: space-between;
    gap: 40px;
    padding-top: 81px;
    /* border: 1px solid red; */
}
.twobtn{
    /* border: 1px solid red; */
    width: 200px;
    margin-top: 20px;
    display: flex;
    justify-content: space-evenly;
    gap: 16px;
}
@media screen and (max-width: 1024px) {
    .home-section {
        width: 100%;
        height: 80vw;
    }
}
@media screen and (max-width: 768px) {
    .home-section {
        width: 100%;
        height: 100vw;
    }
}
@media screen and (max-width: 497px) {
    .home-section {
        width: 100%;
        height: auto;
        flex-direction: column-reverse;
        margin: 40px auto auto;
    }
}
