.contact-head{
    margin: auto;
    margin-top: 10px;
    margin-bottom: 30px;
    text-align: center;
    /* border: 1px solid red; */
}
.container.contact__container{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 12%;
    margin: auto;
}
.contact__options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
.contact__option{
    background: #4db5ff;
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: all 400ms ease;
    /* cursor: pointer; */

}
/* .contact__option:hover{
    background: transparent;
    border-color: rgba(77,181,255,0.4);
} */
.contact__option h5 {
    overflow-wrap: break-word;
}
.icn{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;

}
/* Form */
form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
input,textarea{
    width: 100%;
    padding: 2rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid rgba(77,181,255,0.4);
    resize: none;
    color: white;
}
input[type="text"]{
    padding: 10px;
    box-sizing: border-box;
    /* margin-inline-start: 2px; */
}
input[type="email"]{
    
    padding: 10px;
    box-sizing: border-box;
    /* margin-inline-start: 2px; */
}
.message{
    padding: 20px;
    box-sizing: border-box;
    /* margin-inline-start: 2px; */
}
.btn-primary{
    padding: 3px 3px ;
    background-color: #4db5ff;
    border-radius: 10px;
}
/* /// For Medium Devices */
@media screen and (max-width: 1024px) {


    
 
 }
 /*  For Small Devices */
 @media screen and (max-width: 497px) {
    .container.contact__container{
        grid-template-columns: 1fr;
        gap: 4%;
    }
}