.nav{
  width: 100%;
  z-index: 100;
  position: sticky;
  top: 0;
  background-color: #4db5ff;
  border-bottom:1px solid gray;
  border-color:"gray.100";
 
}
.section{
    margin-left:12px ;
    font-size: 1.2rem;
    font-weight: 600;
  }

  .btncv{
    /* border: 1px solid red; */
    display: flex;
    align-items: center;
    gap: 5px;
  }
  .dlbtn{
    margin-left: 3px;
  }
  html{
    scroll-behavior: smooth;
  }
  .active {
    color: rgb(0, 255, 229);
    text-decoration: none;
  }
  
  .line-icon{
    display: none;
  }
  
  /*  For Small Devices */
  @media screen and (max-width: 768px) {
    
    .nav,.section{
      /* border: 1px solid red; */
      display:none;
      
    }
    .line-icon{
      display: flex;
    }
     
  }
  @media screen and (max-width: 768px) {
    .btncv{
      display: none;
    }
  }
  